@import url(https://fonts.googleapis.com/css2?family=Georama:ital,wght@0,400;0,600;1,400;1,700&family=Open+Sans:ital,wght@0,400;0,700;1,400;1,700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

h1, h2, h3, h4, h5, h6 {
 font-family: 'Georama', sans-serif;
}

p, span, a {
  font-family: 'Open Sans', sans-serif;
}

.App {
  text-align: center;
}

input {
  max-width: 135px;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

.authLink {
  color: darkslategrey
}

.authLink:hover {
  color: darkgray;
}

main {
  margin-top: 64px;
  min-height: 500px;
  z-index: 0;
}

footer {
  display: flex;
  flex-wrap: wrap;
  justify-content: flex-end;
  background-color: rgb(110, 1, 1);
  border-top: ridge 5px rgb(179, 0, 30);
  color: white;
  z-index: 1;
  padding-top: 10px;
  padding-bottom: 20px;
  padding-right: 20px;
  padding-left: 20px;
  text-align: right;
}

footer a {
  color: darkgray;
}

footer a:hover {
  color: darkslategray;
}


.Authentication {
    display: grid;
    grid-template-columns: repeat(12, 1fr);
    width: 100%;
    height: 100%;
    min-height: 500px;
    background-image: url(/static/media/capitol_velazquez_medium_cropped.0e749f2a.jpg);
    background-repeat: no-repeat;
    background-size: auto;

    background-origin: content-box;
    position: relative;
}

.welcome {
    grid-column: 3 / span 6;
    text-align: center;
    -webkit-transform: translateY(30%);
            transform: translateY(30%);
}

.welcome h2 {
    background-color: rgba(255, 255, 255, 0.61);
    padding: 20px;
}

.authDiv {
    grid-column: 10 / span 3;
    border-left: solid white 3px;
    height: 100%;
    background-color: rgba(255, 255, 255, 0.61);
    display: flex;
    justify-content: center;
    
}

.authBox {
    transition: opacity 250ms ease-in-out;
}

.photoCredit {
    position: absolute;
    bottom: 0;

    background-color: rgba(255, 255, 255, 0.61);
}

.photoCredit p {
    padding: 3px;
    margin: 0;
    font-size: small;
}

@media screen and (max-width: 800px) {
    .Authentication {
        display: block;
        width: 100%;
        height: 100%;
        min-height: 500px;
        background-image: url(/static/media/capitol_velazquez_medium_cropped.0e749f2a.jpg);
        background-repeat: no-repeat;
        background-size: auto;
        background-origin: content-box;
    }
    
    .welcome {
        text-align: center;
    }
    
    .welcome h2 {
        background-color: rgba(255, 255, 255, 0.61);
        padding: 20px;
    }
    
    .authDiv {
        display: block;
        border: none;
        background-color: rgba(255, 255, 255, 0.61);
        position: relative;
        display: flex;
        justify-content: center;
        top: 40px;    
    }

    .authBox {
        transition: opacity 250ms ease-in-out;
    }
  }



.Dashboard {
    display: grid;
    width: 100%;
    background-color: rgb(210, 236, 252);
}



.dash-table {
    min-height: 700px;
}

.SearchLegislators {

}

.SearchLegislators p {

}
.listTerm {
    display: flex;
    justify-content: space-between;
    margin-right: 5px;
    margin-left: 5px;
}
.legislatorSearchDisplay {
    display: grid;
    grid-template-columns: 1fr 1fr;
}

.legislatorChamberDisplaySBS {
    display: inline-flex;
    flex-wrap: wrap;
    justify-content: left;
    width: 544px;
}

.legislatorChamberDisplayNonSBS {
    display: inline-flex;
    flex-wrap: wrap;
    justify-content: left;
    width: 1088px;
}

.legisListFullWidth {
    grid-column: 1 / span 2;
}

.legislatorSearchCard {
    width: 250px;
    height: auto;
    display: flex;
    margin: auto;
    margin-top: 5px;
    margin-bottom: 5px;
    border-radius: 5px;
    text-align: left;
    transition: width 400ms ease-in-out, opacity 300ms, margin 400ms ease-in-out;
    border: 1px solid black
}



.legislatorSearchCard img {
    width: 75px;
    border-top-left-radius: 5px;
    border-bottom-left-radius: 5px;
    image-rendering: -webkit-optimize-contrast;
}

.legislatorSearchCard p {
    margin: 0;
    margin-left: 10px;
    padding: 0;
}

.searchCardText {
    width: 175px;
    margin-top: 5px;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}

.legislatorSearchCard strong,
.legislatorSearchCard i,
.legislatorSearchCard a
 {
    margin: 0;
    padding: 0;
    text-decoration: none;
    color: black
}

.searchCardButtons {
    text-align: right;
}

.searchCardButtons button {
    margin-right: 5px;
    margin-bottom: 5px;
}

.legisDisplayCentering {
    display: flex;
    justify-content: center;
}


@media screen and (max-width: 1100px) {
    .legislatorSearchDisplay {
        display: grid;
        grid-template-columns: 1fr;
    }
    
    .sideBySideButton {
        display: none;
    }

    .legislatorChamberDisplayNonSBS {
        display: inline-flex;
        flex-wrap: wrap;
        justify-content: left;
        width: 544px;
    }

    .legisListFullWidth {
        grid-column: 1 / span 1;
    }
  }


@media screen and (max-width: 570px) {
    .legislatorSearchDisplay {
        display: grid;
        grid-template-columns: 1fr;
    }
    
    .sideBySideButton {
        display: none;
    }

    .legislatorChamberDisplayNonSBS {
        display: inline-flex;
        flex-wrap: wrap;
        justify-content: left;
        width: 272px;
    }

    .legisListFullWidth {
        grid-column: 1 / span 1;
    }
  }
.SubjectsList {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
}

.subjectSearchCard {
    display: flex;
    justify-content: space-between;
    width: 240px;
    height: 46px;
    background-color: rgb(253, 249, 188);
    border-radius: 5px;
    text-align: left;
    padding: 10px;
    margin: 10px;
    border: 1px black solid;
    transition: width 400ms ease-in-out, opacity 300ms, margin 400ms ease-in-out, padding 400ms ease-in-out;
}

.subjectSearchCard p {
    margin: 0;
}

.subjectSearchCard button {
    margin: 0;
    margin-left: 5px;
}


.recentBillCard h3, 
.recentBillCard h4 {
    margin: 0;
}

.recentBillCard {
    border: solid black 1px;
    margin: 20px;
    text-align: left;
    padding: 20px;
    border-radius: 5px;
    background-color: white;
}

.recentBillCard p {
    margin: 0;
    padding: 0;
}

.cardHeading {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    margin-bottom: 10px;
}

.cardHeading p {
    text-decoration: underline;
}



.headingButtons button {
    margin-left: 5px;
}

.cardContent {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
}

.cardTitleDiv {
    width: 80%;
}

.cardResultsDiv {

}

.voteCard {
    border: grey 1px solid;
    background-color: rgb(231, 231, 231);
    margin: 5px;
    padding: 5px;
}

.voteHeading {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;

}

.sponsors {
    display: flex;
    flex-wrap: wrap;
    justify-content: flex-start;
}

.positionsList {
    display: flex;
    flex-wrap: wrap;
}

.positionDatum {
    padding: 5px;
    margin: 5px;
    max-width: 250px;
    border: lightgrey 1px solid;
}


@media screen and (max-width: 720px) {
    .cardTitleDiv {
      width: 100%;
    }
  }

.navBar {
    background-color: rgb(110, 1, 1);
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    align-items: center;
    position: fixed;
    top: 0;
    width: 100%;
    border-bottom: ridge 5px rgb(179, 0, 30);
    color: white;
    z-index: 5;
}

.logo {
    margin-left: 20px;
}

.navBar a, .navBar span {
    margin-right: 20px;
    color: white;
    text-decoration: none;
    cursor: default;
}

.navBar a:hover, .navBar span:hover {
    color: rgb(179, 0, 30);
}

.dashnav {
}

.dashNavBar {
    border-left: red 1px solid;
    border-bottom: red 1px solid;
    border-radius: 5px;
    background-color: white;
    padding: 0;
    padding-left: 20px;
    margin: 0;
    position: fixed;
    width: 180px;
    text-align: left;
    z-index: 0;
    transition: right 250ms ease-in-out;
}

.dashNavBar p {
    margin: 0;
}

.dashNavOptions {
    transition: height 250ms ease-in-out, opacity 250ms 100ms;
    margin-bottom: 20px;
}

.dashNavOptions p {
    cursor: default;
}

.dashNavOptions p:hover {
    color: darkslategray;
}

